import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Row, Col, Form, Card } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import { BtnClear } from 'components/common/button';
import BtnExport from 'components/common/button/BtnExport';
import { exportAcademicStatistics } from 'services/studentForAcademicStatistics';

@Form.create({ name: 'ReportAcademicStatisticsPage' })
class FormSearchStuForUnionsMem extends Component {
  state = {};

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.handleExport(values);
    });
  };

  handleExport = async values => {
    try {
      await exportAcademicStatistics(values);
    } catch (error) {
      console.log(error);
    }
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  render() {
    return (
      <Card>
        <Form layout="vertical" className="form-inline-label" onSubmit={this.handleSubmit}>
          <Row gutter={24}>
            <FilterPathEducate form={this.props.form} />
            <YearSemesterInput form={this.props.form} />
            <Col span={6}>
              <div className="btn-group">
                <BtnExport title="Xuất Excel" htmlType="submit" />
                <BtnClear onClick={this.handleReset} />
              </div>
            </Col>
          </Row>
        </Form>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <FormSearchStuForUnionsMem />
  </Page>
);



